import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Calendar, MapPin, Users, Clock, Share2 } from "lucide-react";
import { format } from "date-fns";
import { getRandomAvatar } from "@/lib/random-avatar";

export function EventDetailsDialog({ event, open, onOpenChange, onRegister }) {
  if (!event) return null;

  const attendancePercentage = (event.attendees / event.maxAttendees) * 100;

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[600px] p-6 rounded-lg shadow-lg">
        <DialogHeader>
          <DialogTitle className="text-lg font-semibold">
            {event.title}
          </DialogTitle>
          <DialogDescription className="text-muted-foreground">
            Organized by {event.organizer}
          </DialogDescription>
        </DialogHeader>

        {/* Event Banner */}
        <div className="relative h-48 -mx-6 -mt-2 overflow-hidden bg-gradient-to-r from-red-900 to-red-500 rounded-t-lg">
          <img
            src={`https://picsum.photos/600/300?random=${event.id}`}
            alt={event.title}
            className="w-full h-full object-cover rounded-t-lg"
          />
          <div className="absolute top-4 right-4">
            <Badge
              variant="secondary"
              className="bg-black/50 text-white backdrop-blur-sm px-3 py-1 rounded-full"
            >
              {event.type}
            </Badge>
          </div>
        </div>

        <div className="space-y-6 mt-4">
          {/* Event Details */}
          <div className="grid gap-4">
            <div className="flex items-center text-sm">
              <Calendar className="h-4 w-4 mr-2 text-red-500" />
              <span>
                {format(new Date(event.date), "EEEE, MMMM d, yyyy")} at{" "}
                {event.time}
              </span>
            </div>
            <div className="flex items-center text-sm">
              <MapPin className="h-4 w-4 mr-2 text-red-500" />
              <span>{event.location}</span>
            </div>
          </div>

          {/* Description */}
          <div>
            <h4 className="text-sm font-medium mb-2">About this event</h4>
            <p className="text-sm text-muted-foreground">{event.description}</p>
          </div>

          {/* Attendance */}
          <div>
            <div className="flex items-center justify-between text-sm mb-2">
              <span className="font-medium">Attendance</span>
              <span className="text-muted-foreground">
                {event.attendees} / {event.maxAttendees} spots filled
              </span>
            </div>
            <div className="h-2 w-full bg-slate-200 dark:bg-slate-800 rounded-full">
              <div
                className="h-full bg-gradient-to-r from-red-900 to-red-500 rounded-full transition-all"
                style={{ width: `${attendancePercentage}%` }}
              />
            </div>
          </div>

          {/* Speakers */}
          {event.speakers && event.speakers.length > 0 && (
            <div>
              <h4 className="text-sm font-medium mb-3">Featured Speakers</h4>
              <div className="space-y-3">
                {event.speakers.map((speaker, index) => (
                  <div key={index} className="flex items-center gap-3">
                    <Avatar>
                      <AvatarImage src={getRandomAvatar()} />
                      <AvatarFallback>
                        {speaker.name
                          .split(" ")
                          .map((n) => n[0])
                          .join("")}
                      </AvatarFallback>
                    </Avatar>
                    <div>
                      <p className="font-medium text-sm">{speaker.name}</p>
                      <p className="text-sm text-muted-foreground">
                        {speaker.role}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}

          {/* Tags */}
          <div>
            <h4 className="text-sm font-medium mb-2">Tags</h4>
            <div className="flex flex-wrap gap-2">
              {event.tags.map((tag, index) => (
                <Badge
                  key={index}
                  variant="secondary"
                  className="text-xs px-3 py-1 rounded-full"
                >
                  {tag}
                </Badge>
              ))}
            </div>
          </div>

          {/* Actions */}
          <div className="flex gap-3 pt-4">
            <Button
              className="flex-1 bg-gradient-to-r from-red-900 to-red-500 text-white px-4 py-2 rounded-md shadow-md hover:shadow-lg transition-shadow"
              onClick={onRegister}
            >
              Register Now
            </Button>
            <Button
              variant="outline"
              size="icon"
              className="rounded-md border shadow-sm hover:bg-gray-100 transition-colors"
            >
              <Share2 className="h-4 w-4" />
            </Button>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}
