"use client";

import { useState, useMemo } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Calendar,
  Search,
  Filter,
  MapPin,
  Users,
  CalendarDays,
  CheckCircle,
  XCircle,
} from "lucide-react";

// Import custom components
import { EventCard } from "@/components/events/EventCard";
import { CreateEventDialog } from "@/components/events/CreateEventDialog";
import { EventCalendarView } from "@/components/events/EventCalendarView";
import { EventDetailsDialog } from "@/components/events/EventDetailsDialog";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog";

import { format } from "date-fns";

// Mock data
const initialEvents = [
  {
    id: 1,
    title: "Annual Alumni Networking Night",
    date: "2025-02-25",
    time: "18:00",
    location: "ESPRIT Campus - Block G",
    type: "Networking",
    description:
      "Join us for an evening of networking with fellow alumni and industry professionals. Share experiences, build connections, and explore opportunities.",
    organizer: "Alumni Association",
    attendees: 156,
    maxAttendees: 200,
    tags: ["Networking", "Professional Development"],
    speakers: [
      {
        name: "Sarah Chen",
        role: "AI Research Scientist at Google",
      },
      {
        name: "Mohammed Ali",
        role: "Senior Software Architect at Microsoft",
      },
    ],
    imagePath: "/alumni.png", // Image path added
  },
  {
    id: 2,
    title: "Tech Innovation Workshop",
    date: "2025-03-15",
    time: "14:00",
    location: "ESPRIT Innovation Hub",
    type: "Workshop",
    description:
      "A hands-on workshop exploring the latest technology trends and innovations. Learn about AI, blockchain, and emerging technologies.",
    organizer: "Tech Club",
    attendees: 85,
    maxAttendees: 100,
    tags: ["Technical", "Workshop", "Innovation"],
    speakers: [
      {
        name: "Alex Kumar",
        role: "CTO at TechStartup",
      },
    ],
    imagePath: "/workshop.jpg", 
  },
  {
    id: 3,
    title: "Alumni Hackathon",
    date: "2025-02-22",
    time: "10:00",
    location: "ESPRIT BLOC G",
    type: "Hackathon",
    description:
      "An exciting hackathon where alumni from different fields come together to collaborate on innovative projects. Participants will be grouped into teams to solve real-world challenges within a limited time. It's a great opportunity to network, learn, and build something impactful.",
    organizer: "ESPRIT",
    attendees: 20,
    maxAttendees: 30,
    tags: ["WEB", "Mobile", "Technology"],
    speakers: [
      {
        name: "Taher Belakhdher",
        role: "Founder of esprit",
      },
    ],
    imagePath: "/hack.jpg", 
  },

];


export default function EventsPage() {
  const [events, setEvents] = useState(initialEvents);
  const [searchTerm, setSearchTerm] = useState("");
  const [filterType, setFilterType] = useState("all");
  const [isCreateEventOpen, setIsCreateEventOpen] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [view, setView] = useState("grid"); // grid or calendar
  const [activeTab, setActiveTab] = useState("upcoming");
  const [registrationMessage, setRegistrationMessage] = useState("");
  const [isRegisterDialogOpen, setIsRegisterDialogOpen] = useState(false);
  const [selectedRegisteredEvent, setSelectedRegisteredEvent] = useState(null);
  const [confirmEventId, setConfirmEventId] = useState(null);
  const [isConfirmDialogOpen, setIsConfirmDialogOpen] = useState(false);
  const [selectedConfirmEvent, setSelectedConfirmEvent] = useState(null);

  const handleRegister = (eventId) => {
    const currentEvent = events.find((e) => e.id === eventId);

    if (!currentEvent) {
      setRegistrationMessage("Event not found.");
      setIsRegisterDialogOpen(true);
      setSelectedRegisteredEvent(null);
      return;
    }

    if (currentEvent.attendees >= currentEvent.maxAttendees) {
      setRegistrationMessage("Event is full. Registration failed.");
      setIsRegisterDialogOpen(true);
      setSelectedRegisteredEvent(currentEvent);
      return;
    }

    const updatedEvent = {
      ...currentEvent,
      attendees: currentEvent.attendees + 1,
    };

    setEvents((prevEvents) =>
      prevEvents.map((e) => (e.id === eventId ? updatedEvent : e))
    );

    setRegistrationMessage("Registration successful!");
    setSelectedRegisteredEvent(updatedEvent);
    setIsRegisterDialogOpen(true);
  };

  const handleCloseRegisterDialog = () => {
    setSelectedRegisteredEvent(null);
    setSelectedConfirmEvent(null); // Clear selected confirm event
    setRegistrationMessage("");
    setIsRegisterDialogOpen(false);
  };

  const filteredEvents = useMemo(() => {
    const now = new Date();
    return events.filter((event) => {
      const matchesSearch =
        event.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        event.description.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesType =
        filterType === "all" ||
        event.type.toLowerCase() === filterType.toLowerCase();
      const matchesTab =
        (activeTab === "upcoming" && new Date(event.date) >= now) ||
        (activeTab === "past" && new Date(event.date) < now) ||
        (activeTab === "my-events" && event.organizer === "My Organization");
      return matchesSearch && matchesType && matchesTab;
    });
  }, [searchTerm, filterType, activeTab, events]);

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Events</h1>
          <p className="text-muted-foreground">
            Stay connected with alumni events and gatherings
          </p>
        </div>
        <Button
          className="bg-gradient-to-r from-red-900 to-red-500 text-white"
          onClick={() => setIsCreateEventOpen(true)}
        >
          Create Event
        </Button>
      </div>

      <div className="flex items-center gap-4">
        <div className="relative flex-1">
          <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Search events..."
            className="pl-8"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
        <Select value={filterType} onValueChange={setFilterType}>
          <SelectTrigger className="w-[180px]">
            <SelectValue placeholder="Filter by type" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">All Events</SelectItem>
            <SelectItem value="networking">Networking</SelectItem>
            <SelectItem value="workshop">Workshop</SelectItem>
            <SelectItem value="conference">Conference</SelectItem>
            <SelectItem value="social">Social</SelectItem>
          </SelectContent>
        </Select>
        <div className="border rounded-md p-1">
          <Button
            variant={view === "grid" ? "secondary" : "ghost"}
            size="sm"
            onClick={() => setView("grid")}
          >
            <Users className="h-4 w-4" />
          </Button>
          <Button
            variant={view === "calendar" ? "secondary" : "ghost"}
            size="sm"
            onClick={() => setView("calendar")}
          >
            <CalendarDays className="h-4 w-4" />
          </Button>
        </div>
      </div>

      <Tabs
        value={activeTab}
        onValueChange={setActiveTab}
        className="space-y-6"
      >
        <TabsList>
          <TabsTrigger value="upcoming">Upcoming</TabsTrigger>
          <TabsTrigger value="past">Past Events</TabsTrigger>
          <TabsTrigger value="my-events">My Events</TabsTrigger>
        </TabsList>

        <TabsContent value="upcoming" className="space-y-4">
          {view === "grid" ? (
            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
              {filteredEvents.map((event) => (
                <EventCard
                  key={event.id}
                  event={event}
                  onViewDetails={() => setSelectedEvent(event)}
                  onRegister={() => {
                    setSelectedConfirmEvent(event);
                    setConfirmEventId(event.id);
                    setIsConfirmDialogOpen(true);
                  }}
                />
              ))}
            </div>
          ) : (
            <EventCalendarView
              events={filteredEvents}
              onEventClick={setSelectedEvent}
            />
          )}
        </TabsContent>

        <TabsContent value="past" className="space-y-4">
          {view === "grid" ? (
            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
              {filteredEvents.map((event) => (
                <EventCard
                  key={event.id}
                  event={event}
                  onViewDetails={() => setSelectedEvent(event)}
                  onRegister={() => {
                    setSelectedConfirmEvent(event);
                    setConfirmEventId(event.id);
                    setIsConfirmDialogOpen(true);
                  }}
                />
              ))}
            </div>
          ) : (
            <EventCalendarView
              events={filteredEvents}
              onEventClick={setSelectedEvent}
            />
          )}
        </TabsContent>

        <TabsContent value="my-events" className="space-y-4">
          {view === "grid" ? (
            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
              {filteredEvents.map((event) => (
                <EventCard
                  key={event.id}
                  event={event}
                  onViewDetails={() => setSelectedEvent(event)}
                  onRegister={() => {
                    setSelectedConfirmEvent(event);
                    setConfirmEventId(event.id);
                    setIsConfirmDialogOpen(true);
                  }}
                />
              ))}
            </div>
          ) : (
            <EventCalendarView
              events={filteredEvents}
              onEventClick={setSelectedEvent}
            />
          )}
        </TabsContent>
      </Tabs>

      <CreateEventDialog
        open={isCreateEventOpen}
        onOpenChange={setIsCreateEventOpen}
      />

      <EventDetailsDialog
        event={selectedEvent}
        open={!!selectedEvent}
        onOpenChange={(open) => !open && setSelectedEvent(null)}
        onRegister={() => {
          setSelectedConfirmEvent(selectedEvent);
          setConfirmEventId(selectedEvent.id);
          setIsConfirmDialogOpen(true);
        }}
      />

      <Dialog open={isConfirmDialogOpen} onOpenChange={setIsConfirmDialogOpen}>
        <DialogContent>
          <DialogHeader className="flex flex-col items-center">
            <DialogTitle>Confirm Registration</DialogTitle>
            <DialogDescription>
              Are you sure you want to register for this event?
            </DialogDescription>
          </DialogHeader>
          {selectedConfirmEvent && (
            <div className="bg-white p-6 rounded-xl shadow-lg border border-gray-200 mt-4 space-y-4">
              <div className="flex items-center space-x-4">
                <div className="rounded-full p-4 bg-purple-50">
                  <Users className="h-6 w-6 text-purple-500" />
                </div>
                <div>
                  <h3 className="text-lg font-bold">Event Details</h3>
                  <p className="text-purple-500">
                    {selectedConfirmEvent.title}
                  </p>
                </div>
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <div className="flex items-center text-sm text-muted-foreground">
                    <Calendar className="h-4 w-4 mr-2" />
                    <span>
                      {format(
                        new Date(selectedConfirmEvent.date),
                        "LLL d, yyyy"
                      )}{" "}
                      at {selectedConfirmEvent.time}
                    </span>
                  </div>
                  <div className="flex items-center text-sm text-muted-foreground">
                    <MapPin className="h-4 w-4 mr-2" />
                    <span>{selectedConfirmEvent.location}</span>
                  </div>
                  <div className="flex items-center text-sm text-muted-foreground">
                    <Users className="h-4 w-4 mr-2" />
                    <span>
                      {selectedConfirmEvent.attendees}/
                      {selectedConfirmEvent.maxAttendees} attendees
                    </span>
                  </div>
                </div>
                <div className="col-span-1 md:col-span-2">
                  <p className="text-sm font-medium mb-2">Event Description</p>
                  <p className="text-gray-600">
                    {selectedConfirmEvent.description}
                  </p>
                </div>
              </div>
            </div>
          )}
          <DialogFooter className="flex gap-4">
            <Button
              variant="outline"
              onClick={() => setIsConfirmDialogOpen(false)}
              className="w-full px-6 py-3 rounded-md border border-gray-300 text-gray-800 hover:bg-gray-100 transition-colors"
            >
              Cancel
            </Button>
            <Button
              className="w-full px-6 py-3 rounded-md bg-gradient-to-r from-red-900 to-red-500 text-white hover:from-red-600 hover:to-red-600 transition-colors"
              onClick={() => {
                setIsConfirmDialogOpen(false);
                handleRegister(confirmEventId);
              }}
            >
              Confirm
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      <Dialog
        open={isRegisterDialogOpen}
        onOpenChange={(open) => {
          if (!open) {
            handleCloseRegisterDialog();
          }
        }}
      >
        <DialogContent>
          <DialogHeader className="flex flex-col items-center">
            {registrationMessage === "Registration successful!" ? (
              <CheckCircle className="h-12 w-12 text-green-500 mb-4" />
            ) : (
              <XCircle className="h-12 w-12 text-red-500 mb-4" />
            )}
            <DialogTitle>
              {registrationMessage === "Registration successful!"
                ? "Registration Successful"
                : "Registration Failed"}
            </DialogTitle>
            <DialogDescription
              className={`font-semibold text-lg ${
                registrationMessage === "Registration successful!"
                  ? "text-green-600"
                  : "text-red-600"
              }`}
            >
              {registrationMessage}
            </DialogDescription>
          </DialogHeader>
          {selectedRegisteredEvent && (
            <div className="bg-white p-6 rounded-xl shadow-lg border border-gray-200 mt-4 space-y-4">
              <div className="flex items-center space-x-4">
                <div className="rounded-full p-4 bg-purple-50">
                  <Users className="h-6 w-6 text-purple-500" />
                </div>
                <div>
                  <h3 className="text-lg font-bold">
                    {registrationMessage === "Registration successful!"
                      ? "You are now registered for"
                      : "Registration failed for"}
                  </h3>
                  <p className="text-purple-500">
                    {selectedRegisteredEvent.title}
                  </p>
                </div>
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <div className="flex items-center text-sm text-muted-foreground">
                    <Calendar className="h-4 w-4 mr-2" />
                    <span>
                      {format(
                        new Date(selectedRegisteredEvent.date),
                        "LLL d, yyyy"
                      )}{" "}
                      at {selectedRegisteredEvent.time}
                    </span>
                  </div>
                  <div className="flex items-center text-sm text-muted-foreground">
                    <MapPin className="h-4 w-4 mr-2" />
                    <span>{selectedRegisteredEvent.location}</span>
                  </div>
                  <div className="flex items-center text-sm text-muted-foreground">
                    <Users className="h-4 w-4 mr-2" />
                    <span>
                      {selectedRegisteredEvent.attendees}/
                      {selectedRegisteredEvent.maxAttendees} attendees
                    </span>
                  </div>
                </div>
                <div className="col-span-1 md:col-span-2">
                  <p className="text-sm font-medium mb-2">Event Description</p>
                  <p className="text-gray-600">
                    {selectedRegisteredEvent.description}
                  </p>
                </div>
              </div>
            </div>
          )}
          <DialogFooter className="mt-6">
            <Button
              variant="outline"
              onClick={() => setIsRegisterDialogOpen(false)}
              className="w-full px-6 py-3 rounded-md border border-gray-300 text-gray-800 hover:bg-gray-100 transition-colors"
            >
              Close
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}
