import { useState } from "react";
import { Calendar } from "@/components/ui/calendar";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";
import { MapPin, Clock, Users } from "lucide-react";
import { format, isSameDay } from "date-fns";

export function EventCalendarView({ events, onEventClick }) {
  const [selectedDate, setSelectedDate] = useState(new Date());

  // Get events for selected date
  const selectedDateEvents = events.filter((event) =>
    isSameDay(new Date(event.date), selectedDate)
  );

  // Create an array of dates that have events
  const eventDates = events.map((event) => new Date(event.date));

  return (
    <div className="grid gap-6 md:grid-cols-[400px_1fr]">
      <Card>
        <CardHeader>
          <CardTitle>Calendar</CardTitle>
          <CardDescription>Select a date to view events</CardDescription>
        </CardHeader>
        <CardContent>
          <Calendar
            mode="single"
            selected={selectedDate}
            onSelect={setSelectedDate}
            modifiers={{
              hasEvent: eventDates,
            }}
            modifiersStyles={{
              hasEvent: {
                backgroundColor: "rgb(59 130 246 / 0.1)",
                borderRadius: "100%",
              },
            }}
            className="rounded-md border"
          />
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>
            Events for {format(selectedDate, "MMMM d, yyyy")}
          </CardTitle>
          <CardDescription>
            {selectedDateEvents.length} events scheduled
          </CardDescription>
        </CardHeader>
        <CardContent>
          <ScrollArea className="h-[500px] pr-4">
            <div className="space-y-4">
              {selectedDateEvents.length > 0 ? (
                selectedDateEvents.map((event) => (
                  <div
                    key={event.id}
                    className="flex gap-4 items-start p-4 rounded-lg border hover:bg-accent transition-colors cursor-pointer"
                    onClick={() => onEventClick(event)}
                  >
                    {/* Time Column */}
                    <div className="flex-shrink-0 w-20 text-center">
                      <div className="text-2xl font-bold">
                        {event.time.split(":")[0]}
                        <span className="text-sm font-normal">
                          :{event.time.split(":")[1]}
                        </span>
                      </div>
                      <Badge variant="secondary" className="mt-1">
                        {event.type}
                      </Badge>
                    </div>

                    {/* Event Details */}
                    <div className="flex-1 min-w-0">
                      <h4 className="font-semibold line-clamp-1">
                        {event.title}
                      </h4>
                      <p className="text-sm text-muted-foreground line-clamp-2 mt-1">
                        {event.description}
                      </p>

                      <div className="flex items-center gap-4 mt-3">
                        <div className="flex items-center text-sm text-muted-foreground">
                          <MapPin className="h-4 w-4 mr-1" />
                          <span className="truncate">{event.location}</span>
                        </div>
                        <div className="flex items-center text-sm text-muted-foreground">
                          <Users className="h-4 w-4 mr-1" />
                          <span>
                            {event.attendees}/{event.maxAttendees}
                          </span>
                        </div>
                      </div>

                      {/* Speakers */}
                      {event.speakers && event.speakers.length > 0 && (
                        <div className="flex items-center gap-2 mt-3">
                          <div className="flex -space-x-2">
                            {event.speakers.map((speaker, index) => (
                              <div
                                key={index}
                                className="h-6 w-6 rounded-full border-2 border-background bg-gradient-to-r from-red-900 to-red-500"
                                title={speaker.name}
                              />
                            ))}
                          </div>
                          <span className="text-sm text-muted-foreground">
                            {event.speakers.length} speaker
                            {event.speakers.length > 1 ? "s" : ""}
                          </span>
                        </div>
                      )}
                    </div>

                    {/* Action Buttons */}
                    <div className="flex-shrink-0 self-center">
                      <Button variant="outline" size="sm">
                        Register
                      </Button>
                    </div>
                  </div>
                ))
              ) : (
                <div className="text-center py-8 text-muted-foreground">
                  No events scheduled for this date
                </div>
              )}
            </div>
          </ScrollArea>
        </CardContent>
      </Card>
    </div>
  );
}